<template>

      <div class="box">
        <h3>Not Ekle</h3>
      <form @submit.prevent="onFormSubmit">
        

      <textarea style="width: 100%;"
      rows="4"
       class="form-control"
      placeholder="Not"
      v-model="not.not_icerik"
      required
       ></textarea>

          <input
            type="time"
            class="form-control"
            placeholder="Not Saati"
            v-model="not.not_saati"
            required
          />


 <button class="btn new-add-save">Kaydet</button>


      </form>
  </div>
  
</template>

<script>
import { db } from "../firebaseDb";
export default {
  data() {
    return {
      not: {},
    };
  },
  methods: {
    onFormSubmit(event) {
      event.preventDefault();
      db.collection("notlar")
        .add(this.not)
        .then(() => {
          // alert("User successfully created!");
          this.not.not_icerik = "";
          this.not.not_saati = "";
          this.$router.push("/notlist");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

